.Header {
  position: fixed;
  width: 100%;
  font-size: 0.8rem;
  background-color: rgba(243, 243, 243, 0.7);
  backdrop-filter: blur(2px);
}

.Nav-container {
  display: grid;
  margin-top: 2.5vh;
  margin-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 25%
}

.Nav-name {
  font-style: italic;
}

.Menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 2vw;
  text-transform: uppercase;
}