.Home {
  padding-top: 20vh;
  padding-bottom: 80vh;
}

.Intro-text {
  width: 75%;
  white-space: pre-wrap;
  font-family: 'SK-Modernist';
  font-size: 8vw;
  line-height: 90%;
  margin-left: -0.07em;
  margin-bottom: 0;
}

.intro-text-highlight {
  font-size: 8.15vw;
  display: inline-block !important;
  background-color: transparent !important;
  animation: intro-animation 1s !important;
}

.Intro-animate-1 {
  display: inline-block !important;
  background-color: transparent !important;
  animation: intro-animation 2s !important;
}

.Intro-animate-2 {
  display: inline-block !important;
  background-color: transparent !important;
  animation: intro-animation 1.5s !important;
}

@keyframes intro-animation {
  from {
    bottom: -5%;
    opacity: 0%;
  }
  to {
    bottom: 0%;
    opacity: 100%;
  }
}